<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_sift">
                    <div class="data_s_t">
                        <span>数据筛选</span>
                    </div>
                    <el-row type='flex' justify="space-around">
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-row>
                                    <el-col :span='7'>
                                        <span>岗位名称：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='name'></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-row>
                                    <el-col :span='7'>
                                        <span>岗位类型：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-select v-model="post_t" placeholder="请选择" style='width:100%;'>
                                            <el-option v-for="item in options" :key="item.id" :label="item.typeName" :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='6'>
                            <div class="data_s_c">
                                <el-row>
                                    <el-col :span='7'>
                                        <span>发布时间：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-date-picker style='width:100%;font-size:12px;' v-model="time" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                                        </el-date-picker>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-button type="primary" @click='select' icon="el-icon-search">查询</el-button>
                                <!-- <el-button icon='el-icon-refresh' @click='reset'>重置</el-button> -->
                            </div>
                        </el-col>
                    </el-row>
                    <!-- <el-row type='flex' justify="space-around">
						<el-col :span='5'>
							<div class="data_s_c">
								<el-row>
									<el-col :span='7'>
										<span>状态：</span>
									</el-col>
									<el-col :span='17'>
										<el-input></el-input>
									</el-col>
								</el-row>
							</div>
						</el-col>
						<el-col :span='5'></el-col>
						<el-col :span='6'></el-col>
						<el-col :span='5'></el-col>
					</el-row> -->
                </div>
                <div class="post_content">
                    <div class='post_title'>
                        <span>数据列表</span>
                        <el-button type="primary" @click='add' icon='el-icon-plus'>添加</el-button>
                    </div>
                    <div class="st">
                        <span @click='on' :class='num==0?"active":""'>已发布 ({{list.length}})</span>
                        <span @click='off' :class='num==1?"active":""'>未发布 ({{lists.length}})</span>
                    </div>
                    <!-- 已发布 -->
                    <div v-if='num==0'>
                        <el-table :data="list.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border>
                            <el-table-column prop="id" label="序号">
                            </el-table-column>
                            <el-table-column prop="jobName" label="岗位名称">
                            </el-table-column>
                            <el-table-column prop="typeName" label="岗位类型">
                            </el-table-column>
                            <!-- <el-table-column
    					    prop=""
    					    label="岗位人数">
    					  </el-table-column> -->
                            <el-table-column label="岗位职责" show-overflow-tooltip>
                                <template slot-scope="scope">
                                    <span>{{scope.row.jobRequirement}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="岗位要求" show-overflow-tooltip>
                                <template slot-scope="scope">
                                    <span>{{scope.row.jobStatement}}</span>
                                </template>
                            </el-table-column>
                            <!-- <el-table-column
    					    prop="status"
    					    label="状态"
    					    :formatter='formats'>
    					  </el-table-column> -->
                            <el-table-column prop="creationTime" label="创建时间" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column prop="realName" label="创建人">
                            </el-table-column>
                            <el-table-column label="操作">
                                <template scope='scope'>
                                    <!-- <span style='color:#0079FE;cursor:pointer;margin:0 8px 0 0' @click.stop='edit(scope.$index,scope.row)'><i class="el-icon-edit"></i> 编辑</span> -->
                                    <span style='color:#0079FE;cursor:pointer;' @click.stop='re(scope.$index,scope.row.id)'><i class="el-icon-refresh"></i> 撤回</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="list.length">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                    <!-- 未发布 -->
                    <div v-if='num==1'>
                        <el-table :data="lists.slice((currentPage-1)*pagesize,currentPage*pagesize)" @row-click='handle' style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border>
                            <el-table-column prop="id" label="序号">
                            </el-table-column>
                            <el-table-column prop="jobName" label="岗位名称">
                            </el-table-column>
                            <el-table-column prop="typeName" label="岗位类型">
                            </el-table-column>
                            <!-- <el-table-column
    					    prop=""
    					    label="岗位人数">
    					  </el-table-column> -->
                            <el-table-column label="岗位职责" show-overflow-tooltip>
                                <template slot-scope="scope">
                                    <span>{{scope.row.jobRequirement}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="岗位要求" show-overflow-tooltip>
                                <template slot-scope="scope">
                                    <span>{{scope.row.jobStatement}}</span>
                                </template>
                            </el-table-column>
                            <!-- <el-table-column
    					    prop="status"
    					    label="状态"
    					    :formatter='formats'>
    					  </el-table-column> -->
                            <el-table-column prop="creationTime" label="创建时间" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column prop="realName" label="创建人">
                            </el-table-column>
                            <el-table-column label="操作">
                                <template scope='scope'>
                                    <!-- <span style='color:#0079FE;cursor:pointer;margin:0 8px 0 0' @click.stop='edit(scope.$index,scope.row)'><i class="el-icon-edit"></i> 编辑</span> -->
                                    <span style='color:#0079FE;cursor:pointer;' @click.stop='del(scope.$index,scope.row.id)'><i class="el-icon-delete"></i> 删除</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="lists.length">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '岗位管理',
            currentPage: 1,
            pagesize: 20,
            total: 1,
            post_list: [],
            num: 0,
            name: '',
            post_t: '',
            time: '',
            options: []
        }
    },
    methods: {
        /*查询时间转换*/
        formatDate(date) {
            if (typeof date == "string") {
                return this.formatDate2(date);
            }
            var y = date.getFullYear();
            var M = date.getMonth() + 1;
            M = M < 10 ? "0" + M : M;
            var d = date.getDate();
            d = d < 10 ? "0" + d : d;
            var h = date.getHours();
            h = h < 10 ? "0" + h : h;
            var m = date.getMinutes();
            m = m < 10 ? "0" + m : m;
            var s = date.getSeconds();
            s = s < 10 ? "0" + s : s;
            return y + "-" + M + "-" + d + " " + h + "-" + m + "-" + s;
        },
        /*发布跳转详情*/
        handdle(row, event, column) {
            console.log(row, event, column)
            window.sessionStorage.setItem('post_d', JSON.stringify(row))
            this.$router.push('/home/post_d')
        },
        /*未发布修改*/
        handle(row, event, column) {
            console.log(row, event, column)
            window.sessionStorage.setItem('post_x', JSON.stringify(row))
            this.$router.push('/home/post_edit')
        },
        re(val, id) {
            var _this = this
            this.$confirm('确认撤回, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    _this.$axios.post('/tomlive/talentRecruitment/updateByPrimaryKeyStatus', this.$qs.stringify({
                        jobType: val.post_type,
                        jobName: val.name,
                        jobStatement: val.responsibility,
                        jobRequirement: val.Requirement,
                        talentNumber: val.num,
                        id: id
                    }), {
                        headers: {

                        }
                    }).then((res) => {
                        console.log(res)
                        this.all()
                        /*if (res.status == 200) {
                        	this.$router.push('/home/post_bc_c')
                        } else {
                        	alert('取消撤回')
                        }*/
                    })

                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消撤回'
                });
            })
        },
        /*重置*/
        reset() {
            this.name = '',
                this.post_t = '',
                this.time = ''
        },
        /*查询*/
        select() {
            var a;
            var b;
            if (this.time == '') {
                a = '';
                b = '';
            } else {
                a = this.formatDate(this.time[0])
                b = this.formatDate(this.time[1])
            }
            this.$axios.post('/tomlive/talentRecruitment/selectTalentRecruitmentByCondition', this.$qs.stringify({
                jobName: this.name,
                jobTypeId: this.post_t,
                beginTime: a,
                endTime: b
            })).then((res) => {
                this.post_list = res.data.info
                console.log(this.post_list)
            })
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#F5F5F5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        formats(row, column, value) {
            if (value === 1) {
                return '发布'
            } else {
                return '未发布'
            }
        },
        add() {
            this.$router.push('/home/new_post')
        },
        on() {
            this.num = 0
        },
        off() {
            this.num = 1
        },
        /*删除*/
        del(index, id) {
            console.log(id)
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/talentRecruitment/deleteTalentRecruitmentByPrimaryKey', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        console.log(res)
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        },
        /*编辑*/
        edit(index, val) {
            console.log(index, val)
            window.sessionStorage.setItem('post_x', JSON.stringify(val))
            this.$router.push('/home/post_edit')
        },
        /*查询全部岗位列表*/
        all() {
            this.$axios.post('/tomlive/talentRecruitment/selectAllTalentRecruitment').then((res) => {
                this.post_list = res.data.info.reverse()
                this.total = this.post_list.length
                console.log(this.post_list)
            })
        }
    },
    mounted() {
        this.all()
        this.$axios.post('/tomlive/talentType/selectAllTalentType').then((res) => {
            this.options = res.data.info
        })
    },
    computed: {
        list() {
            var _this = this
            return this.post_list.filter(function(number) {
                return number.status == 1
            })
        },
        lists() {
            var _this = this
            return this.post_list.filter(function(number) {
                return number.status == 0
            })
        }
    }
}
</script>
<style scoped>
.active {
    color: #0079FE;
    border-bottom: 1px solid #0079FE;
}

.data_sift {
    width: 100%;
    height: 140px;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0 0 0;
    border: 1px solid #e9e9e9;
}

.data_s_t {
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    font-size: 14px;
    color: #666;
    font-weight: bold;
    line-height: 50px;
}

.data_s_t>span {
    margin: 0 0 0 20px;
}

.data_s_c {
    width: 100%;
    height: 35px;
    line-height: 35px;
    font-size: 14px;
    margin: 20px 0 0 0;
    color: #666;
}

.post_content {
    width: 100%;
    min-height: 800px;
    background: #fff;
    box-sizing: border-box;
    padding: 0 20px;
    margin: 20px 0;
    border: 1px solid #e9e9e9;
}

.post_title {
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 14px;
    color: #666;
    font-weight: bold;
}

.post_title>button {
    float: right;
    margin: 10px 0px;
}

.pages {
    margin: 20px 0 0 0;
    float: right;
}

.st>span {
    display: block;
    float: left;
    width: 100px;
    height: 45px;
    text-align: center;
    margin: 0 0 20px 0;
    line-height: 45px;
    font-size: 14px;
    cursor: pointer;
}
</style>